import { Component, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';

@Component({
    // tslint:disable-next-line:component-selector
    selector: 'StylishSelectComponent',
    template: `
    <mat-form-field [formGroup]="form">
      <mat-select formControlName="stylishType" placeholder="k tipi seiniz" required>
      <mat-option *ngFor="let stylish of stylishTypes" [value]="stylish.value">{{stylish.text}}</mat-option>
      </mat-select>
    </mat-form-field>`,
})
export class StylishSelectComponent {

    stylishTypes = [
        { value: 'Derecelendirme', text: 'Derecelendirme' },
        { value: 'Yzdesel', text: 'Yzdesel' },
        { value: 'Ak Ulu', text: 'Ak Ulu' },
    ];

    @Input()
    form: FormGroup;
}



